import React, { useEffect, useState } from "react";
import styles from "./index.module.scss";
import CheckBox from "../../../UI/common/CheckBox/CommonCheckBox";
import FlatRow from "./FlatRow";

const RoomsTable = ({
  initialData,
  control,
  tabelHeader,
  tabelHeader2,
  tabelHeader3,
  tabelHeader4,
  tabelHeader5,
  tabelHeader6,
  tabelHeader7,
  errors,
  register,
  getValues,
  setValue,
  onToggleBulkUpload,
  watch,
  setSelectedFlats,
  selectedFlats
}) => {

  const [tableData, setTableData] = useState([]);
  const [showExtendedColumns, setShowExtendedColumns] = useState(false);
  const [showBedColumn, setShowBedColumn] = useState(false);

  useEffect(() => {
    if (initialData?.length) {
      let tempArr = [];
      let isNumberOfRoomAvailable = false, isNumberOfBedsAvailable = false;
      initialData.map(data => {
        let returnObj = data || {};
        if (data?.numberOfRooms != "") isNumberOfRoomAvailable = true;
        if (data?.rooms?.some(room => room.numberOfBeds > 0)) isNumberOfBedsAvailable = true;
        tempArr.push(returnObj)
      });
      setShowExtendedColumns(isNumberOfRoomAvailable);
      setShowBedColumn(isNumberOfBedsAvailable);
      setTableData(tempArr)
    }
  }, [initialData, JSON.stringify(watch("unitDetails"))]);

  return (
    <>
      <div className={styles.tableContainer}>
        <table className={styles.table}>
          <thead>
            <tr className={`${styles.border}`}>
              <th style={{ width: "254px" }}>
                <div className="d-flex ">
                  <div>
                    <CheckBox onChange={(e) => {
                      console.log("checkkkkk e", e)
                    }} />
                  </div>
                  <div className="d-flex px-4 ">{tabelHeader}</div>
                </div>
              </th>
              <th className={`${styles.propertyName} `}>
                <div className={styles.columnContainer}>{tabelHeader2}</div>
              </th>
              {showExtendedColumns && <>
                <th style={{ width: "147px" }}>{tabelHeader4}</th>
                <th style={{ width: "147px" }}>{tabelHeader5}</th>
                {showBedColumn && <th style={{ width: "147px" }}>{tabelHeader6}</th>}
                <th style={{ width: "147px" }}>{tabelHeader7}</th>
              </>}
              <th>{tabelHeader3}</th>
            </tr>
          </thead>
          <tbody>
            {tableData.map((row, index) => {
              return (
                <FlatRow
                  key={`flats-${index}`}
                  fieldName={`unitDetails.${index}`}
                  register={register}
                  watch={watch}
                  setValue={setValue}
                  control={control}
                  setSelectedFlats={setSelectedFlats}
                  showExtendedColumns={showExtendedColumns}
                  showBedColumn={showBedColumn}
                  selectedFlats={selectedFlats}
                />
              )
            })}
          </tbody>
        </table>
      </div>
    </>
  );
};
export default RoomsTable;
